<script>
import { mapState } from 'vuex'
export default {
  name: 'animaciones',
  computed: {
    ...mapState(['animaciones']),
    hhh () {
      const id = parseInt(this.$route.params.id)
      return this.animaciones.filter(function (c) {
        return c.id === id
      })[0]
    }
  }
}
